@font-face {
    font-family: 'Open Sans Light';
    src: local('Open Sans Light'), url(/LoginPage/static/media/OpenSans-Light.9ff12f69.ttf) format('truetype');
}


body {
    font-family: 'Open Sans Light';
}
.header {
  padding-left: 50px; }

.header__logo {
  padding: 30px 0px 0px 100px; }

#searchTerm {
  display: none; }

.search__bar--expanded {
  right: 90px;
  bottom: 75px;
  position: relative; }

.header__searchbar--icon {
  display: block;
  cursor: pointer;
  width: 40px;
  height: 40px;
  background-color: #f0f7fe;
  position: absolute;
  top: 32px;
  right: 32px;
  text-align: center; }
  .header__searchbar--icon img {
    padding-top: 12px; }
  .header__searchbar--icon:hover {
    background-color: #d8eafc; }

#searchTerm:checked ~ .header__searchbar--expanded {
  background-color: #f0f7fe;
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  opacity: 1;
  width: 25%;
  height: 40px;
  right: 70px;
  font-size: 14px; }

.header__searchbar--expanded {
  display: block;
  position: absolute;
  top: 32px;
  right: 96px;
  border: none;
  height: 64px;
  width: 80%;
  padding: 16px 24px;
  font-size: 32px;
  border-bottom: 3px solid transparent;
  -webkit-transform-origin: 100% 50%;
          transform-origin: 100% 50%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  opacity: 0;
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out; }
  .header__searchbar--expanded:focus {
    outline: none;
    border-bottom: 3px solid #f0f7fe; }

@media only screen and (max-width: 600px) {
  .header__logo {
    padding: 20px 0px 0px 0px; }
  .header__searchbar--icon {
    top: 90px; }
  #searchTerm:checked ~ .header__searchbar--expanded {
    width: 70%;
    top: 90px; } }

.login__form__container {
  margin-bottom: 150px;
  border: solid 2px #f6f6f6;
  border-radius: 5px;
  text-align: center; }

.login__form__error--text {
  color: #f6303f;
  font-size: 12px;
  background-color: #fff0f1;
  width: 30%;
  padding: 5px;
  margin-bottom: 0px;
  margin-left: 232px; }

.login__form--text-field {
  width: 300px;
  height: 45px;
  padding: 20px;
  margin-bottom: 25px; }

.login__form__submit-btn {
  display: block;
  width: 72%;
  margin-left: 58px;
  margin-top: 20px;
  padding: 20px;
  background-color: #05549f;
  color: white;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 900; }

.login__form__forgot-password {
  font-size: 12px;
  padding-left: 80px;
  text-decoration: none; }

.login__form__remember__me--label {
  font-size: 12px; }

.login__form__remember__me--text {
  margin: 0;
  display: inline-block;
  padding-left: 10px; }

.login__form--sub-header {
  padding-bottom: 30px; }

.login__form__remember__me--label {
  display: block;
  position: relative;
  padding-left: 35px; }

.login__form__remember__me--label input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0; }

.checkmark {
  position: absolute;
  top: -5px;
  left: 55px;
  height: 25px;
  width: 25px;
  background-color: #eee; }

.login__form__remember__me--label:hover input ~ .checkmark {
  background-color: #ccc; }

.login__form__remember__me--label input:checked ~ .checkmark {
  background-color: #05549f; }

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none; }

/* Show the checkmark when checked */
.login__form__remember__me--label input:checked ~ .checkmark:after {
  display: block; }

/* Style the checkmark/indicator */
.login__form__remember__me--label .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg); }

@media only screen and (max-width: 600px) {
  .login__form__submit-btn {
    margin-left: 11px;
    width: 95%; }
  .checkmark {
    left: 14px; }
  .login__form__error--text {
    margin-left: 8px;
    width: 95%; }
  .login__form__container {
    margin-bottom: 50px; }
  .login__form__forgot-password {
    padding-left: 60px; } }

@media (min-width: 480px) and (max-width: 780px) {
  .checkmark {
    left: 55px; }
  .login__form__submit-btn {
    width: 72%;
    margin-left: 57px; }
  .login__form__error--text {
    margin-left: 56px;
    width: 73%; } }

.products__card--product-name {
  text-align: left;
  padding-left: 115px;
  font-size: 14px;
  color: #414141; }

.products__card--product-price {
  text-align: left;
  padding-left: 115px;
  font-size: 18px;
  font-weight: 900; }

@media (min-width: 480px) and (max-width: 780px) {
  .products__card--product-name,
  .products__card--product-price {
    padding-left: 20px; } }

